import React, { Component } from 'react';
import { Link } from "react-scroll";
import './Menu.css'
import M from "materialize-css";
import { element } from 'prop-types';

let elementInicio;

let elementTestimonios;
let elementContacto;
let elementCitas
let usuario;

class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            logo: process.env.PUBLIC_URL + "/img/logo-regalale3.png",
            gblState: 0,
            element: "",
            componentChange: 0,
        };
    }
    componentDidMount() {
        console.log('estado login ' + this.props.login);
        M.Dropdown.init(this.Dropdown);
        M.Collapsible.init(this.Collapsible);
    }

    componentDidUpdate() {
        M.Dropdown.init(this.Dropdown);
        M.Collapsible.init(this.Collapsible);
    }
    ucword(word) {
        var lsCadena = word.toLowerCase();
        lsCadena = lsCadena.charAt(0).toUpperCase() + lsCadena.slice(1);

        return lsCadena;
    }

    GenerateClick(state, elementMenu) {
        /*
        this.setState({
            gblState: this.props.globalStateComponent,
        })
        */
        this.setState({
            componentChange: state
        })
        this.props.scroolComponent(elementMenu)

        //console.log(element);
        //console.log(this.props.changeComptStateMain);
        //$("#"+context).trigger("click");
    }



    render() {
        let usuario;
        let inicio;
        let inicio_sm;
        if (this.props.login == 0) {
            inicio = <li><a className="modal-trigger" href='#modal1' onClick={() => { this.props.changeOrigen('menu') }}><span id="txt_boton_entrar">Inicia sesión</span></a></li>
            inicio_sm = <a className="modal-trigger hide-on-large-only aling-right" href='#modal1'><span id="txt_boton_entrar">Regístrate</span></a>
        } else {
            inicio = null;
            inicio_sm = null;
        }
        let usuario_sm;
        let cerrar_sesion;
        if (this.props.login == 1) {
            usuario = <li>
                <a id="perfil" className="dropdown-trigger" ref={Dropdown => { this.Dropdown = Dropdown; }} data-target="dropdown1">
                    {this.ucword(this.props.primer_nombre)} <i className="material-icons right">arrow_drop_down</i>
                </a>
                <ul id="dropdown1" className="dropdown-content" >
                    <li><a href="#" onClick={() => { this.props.updateStateComponent(1); this.GenerateClick(1) }} >Perfil</a></li>
                    <li><a href="#" onClick={() => { this.props.updateStateComponent(2); this.GenerateClick(2) }}>Actualizar datos</a></li>
                    <li className="divider"></li>
                    <li><a onClick={() => {
                        this.props.changeLogin(); window.location.reload();
                    }}>Cerrar sesión</a></li>
                </ul>
            </li>
            usuario_sm = <div>

                <li><a href="#" onClick={() => { this.props.updateStateComponent(1); this.GenerateClick(1) }} >Perfil</a></li>
                <li><a href="#" onClick={() => { this.props.updateStateComponent(2); this.GenerateClick(2) }}>Actualizar datos</a></li>
            </div>
            cerrar_sesion =
                <li><a onClick={() => {
                    this.props.changeLogin(); window.location.reload();
                }}>Cerrar sesión</a></li>

        }


        return (
            <div id="menu">
                <div className="navbar-fixed">

                    <nav>
                        <div className="nav-wrapper">
                            <Link
                                id="inicio"
                                activateclass="activate"
                                to="seccion1"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}
                            />
                            <Link
                                id="inicioMain"
                                onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "inicio") }}
                                activateclass="activate"
                                to="seccion1"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}
                            >
                                <img className="brand-logo hide-on-med-and-down logo" alt="logo" src={this.state.logo}>
                                </img>
                            </Link>
                            <a href="#!" data-target="mobile-demo" className="sidenav-trigger">
                                <i className="material-icons">menu</i>
                            </a>
                            {inicio_sm}
                            <ul className=" menuItems right hide-on-med-and-down">
                                <li >
                                    <Link
                                        id="somos"
                                        activateclass="activate"
                                        to="somos"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration={500}
                                    />
                                    <Link
                                        id="somosMain"
                                        onClick={() => { this.props.updateStateComponent(3)}}
                                        activateclass="activate"
                                        to="somos"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration={500}
                                    >
                                        {/*<a href='#/somos'>Quienes somos</a>*/}
                                        Quiénes somos
                                    </Link>
                                </li>
                                    <li id="citas_li">

                                        <Link
                                            id="citas_section"
                                            activateclass="activate"
                                            to="citas"
                                            spy={true}
                                            smooth={true}
                                            offset={-60}
                                            duration={500}

                                        />



                                        <Link
                                            onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "citas_section") }}
                                            activateclass="activate"
                                            to="citas"
                                            spy={true}
                                            smooth={true}
                                            offset={-60}
                                            duration={500}
                                        >
                                            Citas
                                    </Link>
                                    </li>
                                    <li id="testimonios_li">


                                        <Link
                                            id="testimonios_section"
                                            activateclass="activate"
                                            to="testimonios"
                                            spy={true}
                                            smooth={true}
                                            offset={-50}
                                            duration={500}

                                        />


                                        <Link
                                            onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "testimonios_section") }}
                                            activateclass="activate"
                                            to="testimonios"
                                            spy={true}
                                            smooth={true}
                                            offset={-50}
                                            duration={500}
                                        >
                                            Testimonios
                                </Link>
                                    </li>
                                    <li id="blog_li">
                                        <a href="https://www.regalaletiempoatuvida.com/blog" > Blog </a>
                                    </li>
                                    <li id="contacto_li">



                                        <Link
                                            id="contacto_section"
                                            activateclass="activate"
                                            to="contacto"
                                            spy={true}
                                            smooth={true}
                                            offset={-63}
                                            duration={500}

                                        />



                                        <Link
                                            ref={e => (elementContacto = e)}
                                            onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "contacto_section") }}
                                            activateclass="activate"
                                            to="contacto"
                                            spy={true}
                                            smooth={true}
                                            offset={-63}
                                            duration={500}
                                        >
                                            Contacto
                                </Link>
                                    </li>
                                    {inicio}
                                    {usuario}
                                    <li>
                                        <a href="https://www.facebook.com/fundacioninnovagen/" target="_blank">
                                            <i className="fa fa-facebook fa-lg"></i>
                                        </a>
                                    </li>
                                    <li>
                                        <a href="https://instagram.com/funinnovagen?igshid=1i9a8bm9m2kck" target="_blank">
                                            <i className="fa fa-instagram fa-lg"></i>
                                        </a>
                                    </li>
                                    <li>
                                        <a id="iconWSP" href="https://api.whatsapp.com/send?phone=573174412170" target="_blank">
                                            <i className="fa fa-whatsapp fa-lg"></i>
                                        </a>
                                    </li>

                            </ul>
                        </div>
                    </nav>

                    
                </div>

                    <Link
                        id="calculadora_section"
                        activateclass="activate"
                        to="contenedor-calculadora"
                        spy={true}
                        smooth={true}
                        offset={-23}
                        duration={500}

                    />

                    <Link
                        id="scroolCalculadora_sm"
                        activateclass="activate"
                        to="contenedor-calculadora"
                        spy={true}
                        smooth={true}
                        offset={-55}
                        duration={500}

                    />
                    <Link
                        id="citas_sm"
                        activateclass="activate"
                        to="citas"
                        spy={true}
                        smooth={true}
                        offset={-60}
                        duration={500}

                    />
                    <Link
                        id="scroolTestimonios_sm"
                        activateclass="activate"
                        to="testimonios"
                        spy={true}
                        smooth={true}
                        offset={-50}
                        duration={500}

                    >
                    </Link>
                    <Link
                        id="scroolContacto_sm"
                        activateclass="activate"
                        to="contacto"
                        spy={true}
                        smooth={true}
                        offset={-63}
                        duration={500}

                    >
                    </Link>

                    <ul className="sidenav" id="mobile-demo">
                        {usuario_sm}

                        <li id="citas_li_sm">

                            <li >
                                <a href='#/somos'>Quiénes somos</a>
                            </li>

                        </li>

                        <li id="citas_li_sm">

                            <Link

                                onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "citas_sm") }}
                                activateclass="activate"
                                to="citas"
                                spy={true}
                                smooth={true}
                                offset={-60}
                                duration={500}
                            >
                                Citas
                        </Link>

                        </li>
                        <li id="testimonios_li_sm">

                            <Link
                                onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "scroolTestimonios_sm") }}
                                activateclass="activate"
                                to="testimonios"
                                spy={true}
                                smooth={true}
                                offset={-50}
                                duration={500}
                            >
                                Testimonios
                                </Link>
                        </li>
                        <li id="blog_li_sm">



                            <a href="https://www.regalaletiempoatuvida.com/blog" > Blog </a>

                        </li>
                        <li id="contacto_li_sm">

                            <Link

                                onClick={() => { this.props.updateStateComponent(0); this.GenerateClick(0, "scroolContacto_sm") }}
                                activateclass="activate"
                                to="contacto"
                                spy={true}
                                smooth={true}
                                offset={-63}
                                duration={500}
                            >
                                Contacto
                                </Link>
                        </li>






                        {cerrar_sesion}
                        <li>

                            <div className="row ">
                                <div className="col s6 offset-s3">

                                    <div className="col s4">
                                        <a href="https://www.facebook.com/fundacioninnovagen/" target="_blank">
                                            <i className="fa fa-facebook fa-lg"></i>
                                        </a>

                                    </div>
                                    <div className="col s4">
                                        <a href="https://instagram.com/funinnovagen?igshid=1i9a8bm9m2kck" target="_blank">
                                            <i className="fa fa-instagram fa-lg"></i>
                                        </a>
                                    </div>
                                    <div className="col s4">
                                        <a id="iconWSP" href="https://api.whatsapp.com/send?phone=573174412170" target="_blank">
                                            <i className="fa fa-whatsapp fa-lg"></i>
                                        </a>
                                    </div>
                                </div>

                            </div>


                        </li>

                    </ul>

                </div>



                );
        
        
            }
        
        
        
        }
function Perfil() {

                }
                export default Menu; // Don’t forget to use export default!